.parallax {
  position: fixed;
  z-index: 1; }

@-webkit-keyframes animStar {
  from {
    -webkit-transform: translateY(-2000px);
            transform: translateY(-2000px); }
  to {
    -webkit-transform: translateY(0px);
            transform: translateY(0px); } }

@keyframes animStar {
  from {
    -webkit-transform: translateY(-2000px);
            transform: translateY(-2000px); }
  to {
    -webkit-transform: translateY(0px);
            transform: translateY(0px); } }



